.about-me {
    margin-top: 24px;
    margin-left: 16vw;
    width: 74vw;
    color: #c2c1c0;
    font-size: calc(8px + 1.5vmin);
}

.about-me-link:visited {
    color: rgb(90, 90, 232);
}

.about-me-link:hover {
    border-bottom: 1px solid;
    margin-bottom: -1px;
    background: rgb(28, 28, 134);
}


.about-me-profile-link:link {
    color: #CDFEAA;
}

.about-me-profile-link:visited {
    color: #CDFEAA;
}

.about-me-profile-link:focus {
    border-bottom: 1px solid;
    margin-bottom: -1px;
    background: #BAE498;
}

.about-me-profile-link:hover {
    border-bottom: 1px solid;
    margin-bottom: -1px;
    background: #437A16;
}

.about-me-profile-link:active {
    background: #CDFEAA;
    color: #437A16;
}

.about-me-profile-links {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}